import React from "react";
import classes from "./Users.module.css";
import {NavLink} from "react-router-dom";

let User = ({user,followingInProgres,unfollow,follow}) => {
    return (
        <div key={user.id} className={classes.userItem}>
            <div className={classes.userAva}>
                <div>
                    <NavLink to={'/profile/' + user.id}>
                        <img alt={user.name}
                             src={user.photos.small != null ? user.photos.small : process.env.PUBLIC_URL + "/assets/img/avatars/default_user.png"}/>
                    </NavLink>
                </div>
                {user.followed

                    ? <button disabled={followingInProgres
                        .some(id => id === user.id)}
                              onClick={() => {
                                  unfollow(user.id)
                              }}>Unfollow</button>

                    : <button disabled={followingInProgres
                        .some(id => id === user.id)}
                              onClick={() => {
                                  follow(user.id)
                              }}>Follow</button>}

            </div>
            <div className={classes.userInfo}>
                <div className={classes.userName}>
                    <p>{user.name}</p>
                    <div>{user.status}</div>
                </div>
                <div className={classes.userLocation}>
                    <p>{'user.lookingForAJob'}</p>
                    <div>{'user.location.city'}</div>
                </div>
            </div>
        </div>

    )

}

export default User